import axios from "axios"
const url ='http://localhost:3006';
console.log(url)


export const get = () => (dispatch) => {
    axios.get('http://localhost:3006/gemstone').then((res) => {
        dispatch({
            type: 'Get-GEMSTONE',
            payload:  res.data,
        })
    })
}
export const getHorescope = () => (dispatch) => {
    axios.get('http://localhost:3006/horescope').then((res) => {
        dispatch({
            type: 'Get-HORESCOPE',
            payload:  res.data,
        })
    })
}
export const getById = (id) => (dispatch) => {
    axios.get('http://localhost:3006/gemstone').then((res) => {
        let data = res.data
        const result = data.filter(word => word.id ==id);
        dispatch({
            type: 'Get-GEMSTONE',
            payload: result,
        })
    })
}
